import './game.scss';
import Screen from "./Screen";
import Canva from "./Canva";
import { useState, useEffect } from "react";

export default function Game(){

    const [idPkm, setIdPkm] = useState(0);
    const [dataPkm, setDataPkm] = useState(null);
    const [rs, setRs] = useState(0);
    const [intro, setIntro] = useState(null);
    const [ju, setJu] = useState(null);
    const [jd, setJd] = useState(null);
    const [upok, setUPok] = useState(new Blob());
    const [dpok, setDPok] = useState(new Blob());
    const [answerGived, setAnswerGiver] = useState(true);
    const [resu, setResU] = useState(null);
    const [resd, setResD] = useState(null);
    const [mess1, setMess1] = useState("");
    const [mess2, setMess2] = useState("");
    const [name, setName] = useState("");
    const [scoreu, setScoreu] = useState(0);
    const [scored, setScored] = useState(0);

    function submit(id){
        const val = parseInt(document.querySelector('#'+id).value);
        if(id === "u"){
            setResU(val);
        }else{
            setResD(val);
        }
        
    }

    function generate(){
        (async () => {
            let id;
            do{
                id = Math.round(Math.random()*810);
            }while(id === idPkm && id !== 0);
            const response = await fetch(`https://pokeapi.co/api/v2/pokemon-species/${id}/`);
            const json = await response.json();
            const responseD = await fetch(`https://pokeapi.co/api/v2/pokemon/${id}/`);
            const jsonD = await responseD.json();
            setName(json.names[4].name);
            setDataPkm(jsonD);
            setIdPkm(id);
            setRs(Math.round(Math.random()*5));
            setAnswerGiver(false);
            setMess1("");
            setMess2("");
            document.querySelector('#u').disabled = false;
            document.querySelector('#d').disabled = false;
        })();
    }

    function register(){
        setJu(document.getElementById("ju").value);
        setJd(document.getElementById("jd").value);
        setIntro(true);
        generate();
    }

    function generateImage(i){
        (async () => {
            const response = await fetch(`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${Math.round(Math.random()*810)}.png`);
            if(i === "1") setUPok(await response.blob()); else setDPok(await response.blob()); 
        })();
    }

    useEffect(() => {
        (async () => {
            const responseU = await fetch(`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${Math.round(Math.random()*810)}.png`);
            setUPok(await responseU.blob());
            const responseD = await fetch(`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${Math.round(Math.random()*810)}.png`);
            setDPok(await responseD.blob()); 
        })();
    }, []);
    
    useEffect(() => {
        if(resu !== null && resd !== null){
            const w = "Flicitations tu as trouv la parfaite statistique du pokmon !";
            const l = "Dommage... Peut-tre que tu trouveras une prochaine fois... Qui sait ? \\_()_/";
            const stat = dataPkm.stats[rs].base_stat;
            setMess1(l);
            setMess2(l);
            if(resu === stat){
                setMess1(w);
                setScoreu(scoreu+1);
            }
            if(resd === stat){
                setMess2(w);
                setScored(scored+1);
            }
            setResU(null);
            setResD(null);
            setAnswerGiver(true);
        }
    }, [resu, resd, rs, dataPkm, scoreu, scored]);
    
    return(
        <>
            {
                intro === null || ju === null || jd === null ?
                    <div id="intro">
                        <img src="https://cdn.shopify.com/s/files/1/0252/3362/1040/files/gif_pokeball_56f0db45-6adb-4965-a3a7-6e66c96e32ec_480x480.gif" alt="chargement"/>
                        <div className="flex">
                            <div className="choose-j">
                                <input type="text" maxLength="10" placeholder="Pseudo joueur n1" id="ju" defaultValue="j1"/>
                                <img src={URL.createObjectURL(upok)} alt="Pokmon joueur 1"/>
                                <button onClick={() => generateImage("1")} className="btn-rand-pok">Generate new icon</button>
                            </div>
                            <div className="choose-j">
                                <input type="text" maxLength="10" placeholder="Pseudo joueur n2" id="jd" defaultValue="j2"/>
                                <img src={URL.createObjectURL(dpok)} alt="Pokmon joueur 2"/>
                                <button onClick={() => generateImage("2")} className="btn-rand-pok">Generate new icon</button>
                            </div>
                        </div>
                        <button onClick={() => register()}>Commencer  jouer !</button>
                    </div>
                    :
                    <div id="game">
                        <Screen submit={() => submit("u")} hiddenStat={rs} id={"u"} pseudo={ju} img={upok} mess={mess1} score={scoreu}/>
                        <Canva newPkm={() => generate()} data={dataPkm} hiddenStat={rs} show={answerGived} name={name}/>
                        <Screen submit={() => submit("d")} hiddenStat={rs} id={"d"} pseudo={jd} img={dpok} mess={mess2} score={scored}/>
                    </div>
            }
        </>
    );
}