import './canva.scss';
import {useEffect, useState} from "react";
import Chart from "./Chart.tsx";

export default function Canva(props){

    const [blob, setBlob] = useState(null);
    const [dataPkm, setDataPkm] = useState(null);
    const [stats, setStats] = useState(null);

    useEffect(() => {
        if(props.data !== null) {
            setDataPkm(props.data);
            (async () => {
                const response = await fetch(`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${props.data.id}.png`);
                setBlob(await response.blob());
            })();
            setStats({
                p: props.data.stats[0].base_stat,
                a: props.data.stats[1].base_stat,
                d: props.data.stats[2].base_stat,
                as: props.data.stats[3].base_stat,
                ds: props.data.stats[4].base_stat,
                v: props.data.stats[5].base_stat
            });
        }
    }, [props]);

    return (
        <div id="canva">
            {
                dataPkm !== null && blob !== null && stats !== null ?
                <>
                    <img id="imgPkm" src={URL.createObjectURL(blob)} alt={dataPkm.forms[0].name}/>
                    <h3 style={{
                        color: "black",
                        textTransform: "uppercase",
                        fontSize: "2rem",
                        marginBottom: "20px"
                    }}>{props.name}</h3>
                    <div id="cadreStats">
                        <Chart data={stats} hiddenStat={props.hiddenStat} show={props.show}/>
                    </div>
                </>
                :
                null
            }
            {
                props.show ?
                <button onClick={() => props.newPkm()}>Rechercher un nouveau pokmon</button>
                :
                null
            }
        </div>
    );
}