import './screen.scss';

export default function Screen(props){

    function dis(){
        document.querySelector(`#${props.id}`).disabled = true
    }

    return (
        <div className="content">
            <h4>{props.mess}</h4>
            <img src={URL.createObjectURL(props.img)} alt="Pokmon joueur"/>
            <h3>{props.pseudo.toUpperCase()}</h3>
            <h6>Score: {props.score}</h6>
            <input type="number" placeholder="100" id={props.id} max={999} min={0} defaultValue={0}/>
            <button onClick={() => {props.submit();dis();}}>Valider la saisie</button>
        </div>
    );
}