import React from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

const labels = ['PV', 'Attaque', 'Dfense', 'Attaque spciale', 'Dfense spciale', 'Vitesse'];

export default function Chart(props){

    const options = {
        indexAxis: 'y' as const,
        elements: {
            bar: {
                borderWidth: 1,
            },
        },
        responsive: true,
        plugins: {
            legend: {
                position: 'right' as const,
                display: false
            }
        },
        animation: {
            duration: 0
        }
    };

    let tmpBg = ['#A0F0A0', '#F0A0A0', '#F0F0A0', '#F8C8A0', '#E8C868', '#A0F0F0'];
    let tmpBorder = ['black', 'black', 'black', 'black', 'black', 'black'];
    if(!props.show){
        tmpBg[props.hiddenStat] = "rgba(0, 0, 0, 0)";
        tmpBorder[props.hiddenStat] = "rgba(0, 0, 0, 0)";
    }
    let data = {
        labels,
        datasets: [
            {
                label: 'Dataset 1',
                data: [props.data.p, props.data.a, props.data.d, props.data.as, props.data.ds, props.data.v],
                borderColor: tmpBorder,
                backgroundColor: tmpBg,
            }
        ],
    };

    return <Bar options={options} data={data} />;
}